/* page-loading-indicator rules work with the function by the same name
in mapbox-react-components */
@keyframes progress-cycle {
  0% { background-color: var(--blue-dark); }
  100% { background-color: var(--purple-dark); }
}

.page-loading-indicator {
  transform: translate3d(-100%, 0, 0);
  transition: all 150ms ease-in;
  animation: progress-cycle 0.75s ease-in-out alternate-reverse infinite;
  width: 200%;
  z-index: 9999;
}

.page-loading-indicator-enter {
  /* -10%, instead of 0%, keeps the rounded corners offscreen */
  transform: translate3d(-10%, 0, 0);
  transition: all 150ms ease-in;
}

.page-loading-indicator-leave {
  transform: translate3d(100%, 0, 0);
  transition: all 150ms ease-in;
}

/* Styling for Radix UI specific markup found in @mapbox/mr-ui
 * See variants.css for the accompanying color selectors.
 * */

/* Highlighted keyboard states for context menu component */
[role='menuitem'][data-highlighted] {
  color: var(--blue);
  background-color: var(--gray-lighter);
}
[role='menuitem'][data-state='open'] {
  color: var(--blue);
  background-color: var(--gray-faint);
}

/* Switch & range components */
.switch[data-state='checked']::after {
  left: 50%;
  background-color: var(--white);
}

.switch[data-state='checked'] {
  border-color: var(--transparent);
  background-color: var(--gray);
}

.switch[data-disabled] {
  border-color: var(--transparent);
  background-color: var(--disabled25);
  pointer-events: none;
  color: var(--darken25);
}

.switch[data-disabled]::after {
  background-color: var(--disabled25);
}

.range > [data-orientation='horizontal'],
.range [role='slider'] {
  border-color: var(--transparent);
  color: var(--black);
}

.range [role='slider']:focus {
  box-shadow: inset 0 0 0 1px var(--gray), var(--focus-shadow);
}

.range > [data-orientation='horizontal'] {
  position: relative;
  display: flex;
  align-items: center;
  user-select: none;
  touch-action: none;
  width: 100%;
  height: 20px;
  cursor: pointer;
}

.range.range--s > [data-orientation='horizontal'] {
  height: 12px;
}

.range > [data-orientation='horizontal'] > [data-orientation='horizontal'] {
  position: relative;
  height: 4px;
  border-radius: 2px;
  background: currentColor;
  flex-grow: 1;
}

.range.range--s > [data-orientation='horizontal'] > [data-orientation='horizontal'] {
  height: 2px;
}

.range [role='slider'] {
  transition: background var(--transition);
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid currentColor;
  background: var(--white);
  cursor: grab;
}

.range [role='slider']:focus {
  cursor: grabbing;
}

.range--s [role='slider'] {
  width: 12px;
  height: 12px;
}

.range [data-disabled] [data-orientation='horizontal'] {
  background: var(--disabled25);
}

.range [data-disabled] [role='slider'] {
  border-color: var(--disabled25);
  background: var(--gray-faint);
}
